@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Book.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Heavy.ttf);
}

* {
  box-sizing: border-box;
}

.form {
  margin-top: 20px;
  position: relative;
}

.field {
  font-family: "Avenir", Arial, Helvetica, sans-serif;
  font-weight: 300;
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  font-size: 14px;
  line-height: 19px;
  width: 100%;
  height: 42px;
  padding-left: 33px;
}

.field::placeholder {
  color: #3d4e61;
}

.field:focus {
  outline: none;
  font-weight: 400;
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  background-blend-mode: normal;
}

.field:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.bigSearch {
  font-size: 16px;
  line-height: 22px;
  width: 100%;
  height: 70px;
  padding-left: 62px;
  position: relative;
}

.bigLabel::after {
  content: "";
  position: absolute;
  background-image: url("../src/images/Search.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  top: 25px;
  left: 25px;
  height: 19px;
  width: 19px;
}

.smallLabel::after {
  content: "";
  position: absolute;
  background-image: url("../src/images/Search.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  top: 15px;
  left: 13px;
  height: 11px;
  width: 11px;
}
